import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
import {ConfigService} from '../config.service'
import {post} from '../detail'
@Component({
  selector: 'app-detail',
  templateUrl: './detail.page.html',
  styleUrls: ['./detail.page.scss'],
})
export class DetailPage implements OnInit {
  sub: any;
  name: string;
  mail: string;
  uid: string;
  post:any;
  gender: any;
  books: any;
  actvities: any;
  meet: any;
  status: any;
  long: any;
  edu: Object;
  smoke: any;

  field_gender:Array<any>;
  field_favorite_books:Array<any>;
  field_activities_interests:Array<any>;
  field_look_meet:Array<any>;
  field_relationship_status:Array<any>;
  field_long_in_city:Array<any>;
  field_education_level:Array<any>;
  field_smoke:Array<any>;
  picture:Array<any>
  postman:any ={};
  Books: any;
  constructor(private _Activatedroute: ActivatedRoute,
    private ConfigService:ConfigService) { }
  
  ngOnInit() {
    this.sub = this._Activatedroute.paramMap.subscribe((params) => {
      this.name = params.get("name");
      this.mail = params.get("mail");
      this.uid = params.get("uid");
    });


            this.ConfigService.getUser(this.uid).subscribe((data) => {
              this.post = data;
              console.log(this.post)
              this.picture = this.post.picture.url
              this.long = this.post.field_long_in_city.length;
              this.gender = this.post.field_gender.und[0].value;
              this.books =this.post.field_favorite_books.und[0].value;
              this.actvities =this.post.field_activities_interests.und[0].value;
             this.meet = this.post.field_look_meet.und[0].value;
             this.status =this.post.field_relationship_status.und[0].value;
             this.edu = this.post.field_education_level.und[0].value;
             this.smoke = this.post.field_smoke.und[0].value;
           console.log(this.status)

           
    });
    this.ConfigService.getDetail().subscribe((element) => {
      this.postman = element;
      console.log(this.postman.Activities)
});
   
  }

}
